            <view  class="single-goods"  wx:for="{{goodsList}}"  wx:key="index" bindtap='toGoodsDetails' data-id='{{item.commodityId}}' style="position: relative;">
              <view	style="color:#fff;background:#ff0000;border-top-left-radius:15rpx;font-size:22rpx;padding: 6rpx 25rpx;position: absolute;top: 0;border-bottom-right-radius: 50rpx;">{{btnTitle}}</view>
