                <view class="single-goods" @tap="toGoodsDetails" :data-id="item.commodityId" style="position: relative" v-for="(item, index) in goodsList" :key="index">
                    <view
                        style="
                            color: #fff;
                            background: #ff0000;
                            border-top-left-radius: 15rpx;
                            font-size: 22rpx;
                            padding: 6rpx 25rpx;
