<script>
// import vanCountDown from '../../../miniprogram_npm/@vant/weapp/count-down/index';
import addSubtractNum from '@/pages/Component/add-subtract-num/add-subtract-num';
let app = getApp();
export default {
    components: {
        // vanCountDown,
        addSubtractNum
    },
    data() {
        return {
            tempId: 1,
            btnTitle: '',
            //
            classItem: '',
            navList: [],
            //同城、预定分类列表
            goodsList: [],
            pageSize: 10,
            skuList: [],
            //规格列表
            nowSku: {},
            //选中的规格
            goodsData: {},
            //同城、预定分类商品信息
            goodsItem: {},
            //正在操作的商品信息
            currentIndex: 0,
            JifentimeData: {},
            footImg: '/static/pages/static/foot_Img.png',
            isCountDesabled: false,
            buyCount: 1,
            //购买数量
            showBuyPopup: false,
            orderTemplate: '' //订单模板 1.物流 2.同城 3.预订
        };
    },
    /**
     * 组件的属性列表
     */
    props: {
        isWuliu: {
            type: Boolean,
            default: false
        },
        //物流
        isToCity: {
            type: Boolean,
            default: false
        },
        //同城
        isToStore: {
            type: Boolean,
            default: false
        },
        //预订
        mainOrderType: {
            type: String,
            default: ''
        },
        //主推业务 1物流 2同城 3预订
        productList: {
            type: Array,
            default: () => []
        },
        tempId: {
            type: [String, Number],
            default: 1
        },
        marchantId: {
            type: String,
            default: ''
        },
        orderSwitch: {
            type: [Number, String]
        },
        countdown: {
            type: Boolean,
            default: false
        }
    },
    beforeMount: function () {
        console.log('123', this.isToCity);
    },
    /* 监听数据变化 */
    watch: {
        // 'tempId':function(newVal,oldVal){
        //     if(newVal==1 || newVal==4){
        //         var orderTemplate='';//订单模板 1.物流 2.同城 3.预订
        //         newVal=Number(newVal);
        //         switch(newVal){
        //             case 1: orderTemplate=3;break;
        //             case 4: orderTemplate=2;break;
        //         }
        //         this.setData({orderTemplate},()=>{
        //             this.getClassifyNavList();
        //         });
        //     }
        // },
        mainOrderType: {
            handler: function (nowVal, oldVal) {
                if (nowVal) {
                    var btnTitle = '';

                    switch (Number(nowVal)) {
                        case 1:
                            btnTitle = '物流发货';
                            break;

                        case 2:
                            btnTitle = '同城配送';
                            break;

                        case 3:
                            btnTitle = '门店团购';
                            break;
                    }

                    this.setData(
                        {
                            orderTemplate: nowVal,
                            btnTitle
                        },
                        () => {
                            this.getClassifyNavList();
                        }
                    );
                }
            },

            immediate: true,
            deep: true
        }
    },
    /**
     * 组件的方法列表
     */
    methods: {
        changeJfTime(e) {
            this.JifentimeData = e.detail;
            this.setData({
                JifentimeData: this.JifentimeData
            });
        },

        titleClick(e) {
            var item = e.currentTarget.dataset.idx;
            this.setData(
                {
                    currentIndex: item
                },
                () => {
                    this.getProductList();
                }
            );
        },

        toGoodsDetails(e) {
            var id = e.currentTarget.dataset.id; // 商品ID

            var mainOrderType = this.mainOrderType; // 商家经营模式

            var url = '/pages/Index/GoodsDetails/GoodsDetails?id=' + id;

            if (mainOrderType == 2 || mainOrderType == 3) {
                if (mainOrderType == 2) {
                    url += `&city=1`;
                } //同城

                if (mainOrderType == 3) {
                    url += `&reserve=1`;
                } //预订
            }

            uni.navigateTo({
                url
            });
        },

        navito(e) {
            const { name, id } = e.currentTarget.dataset;
            uni.navigateTo({
                url: `/pages/Index/GoodsList/GoodsList?category=${name}&marchantId=${this.marchantId}&classifyId=${id}`
            });
        },

        goColumn(e) {
            const { id, name } = e.currentTarget.dataset;
            var marchantId = this.marchantId;
            var mainOrderType = this.orderTemplate;
            var query = `?id=${id}&marchantId=${marchantId}&title=${name}&mainOrderType=${mainOrderType}`;
            uni.navigateTo({
                url: '/pages/shopHome/column/column' + query
            });
        },

        jumpDetail(e) {
            var item = e.currentTarget.dataset.item;
            var orderTemplate = this.orderTemplate;
            var { marchantId } = this;
            var commodityId = item.commodityId;
            var url = '/pages/special_goods/goods_detail/goods_detail?';
            url += `commodityId=${commodityId}&marchantId=${marchantId}&orderTemplate=${orderTemplate}`;
            uni.navigateTo({
                url
            });
        },

        temp1SwitchNav(e) {
            //切换分类导航
            var item = e.currentTarget.dataset.item;
            this.setData(
                {
                    classItem: item
                },
                () => {
                    this.getProductList();
                }
            );
        },

        countChange(e) {
            //购买数量改变
            var buyCount = e.detail.value;
            var currentSku = this.nowSku;

            if (buyCount > currentSku.stock) {
                this.setData({
                    isCountDesabled: true
                });
                return uni.showToast({
                    title: '购买数量超出库存数量',
                    icon: 'none'
                });
            }

            this.setData({
                buyCount
            });
        },

        minusCount() {
            if (this.isCountDesabled) {
                this.setData({
                    isCountDesabled: false
                });
            }
        },

        openBuyPopup(e) {
            //打开购买弹窗
            var goodsItem = e.currentTarget.dataset.goodinfo;
            this.querySkuList(goodsItem);
        },

        closeBuyPopup() {
            //关闭购买弹窗
            this.setData({
                showBuyPopup: false
            });
        },

        getClassifyNavList() {
            //获取分类列表
            var { marchantId } = this;
            app.globalData
                .sjrequest('/commodity/queryClassify', {
                    marchantId,
                    orderTemplate: this.orderTemplate
                })
                .then((res) => {
                    if (res.statusCode == 200 && res.data.code == 200) {
                        var list = res.data.data || [];
                        var classItem = list[0] || {};
                        var goodsData = this.goodsData;
                        list.forEach((item) => {
                            goodsData['listInfo' + item.id] = {
                                stopReq: false,
                                pageNum: 1,
                                list: []
                            };
                        });
                        this.setData(
                            {
                                navList: list,
                                classItem
                            },
                            () => {
                                this.getProductList();
                            }
                        );
                    }
                });
        },

        getProductList() {
            //查询 预订与同城 商品
            var { marchantId, mainOrderType } = this;
            var pageSize = this.pageSize;
            var classifyId = this.classItem.id;
            var goodsData = this.goodsData;
            var goodsItem = goodsData['listInfo' + classifyId];
            app.globalData
                .sjrequest('/commodity/queryCommodityList', {
                    marchantId,
                    // classifyId,   // 不传此ID，返回所有分类下的所有商品
                    pageCurr: goodsItem.pageNum,
                    // pageSize,
                    orderTemplate: this.orderTemplate
                })
                .then((res) => {
                    if (res.statusCode == 200 && res.data.code == 200) {
                        var list = res.data.data || [];
                        goodsItem.stopReq = list.length !== pageSize;

                        if (goodsItem.pageNum == 1) {
                            goodsItem.list = list;
                        } else {
                            goodsItem.list.push(...list);
                        }

                        this.setData({
                            goodsData,
                            goodsList: goodsItem.list
                        });
                    }
                });
        },

        querySkuList(goodsItem) {
            //查询规格列表
            var { marchantId } = this;
            app.globalData
                .sjrequest('/commodity/queryCommoSku', {
                    marchantId,
                    commodityId: goodsItem.commodityId
                })
                .then((res) => {
                    if (res.statusCode == 200 && res.data.code == 200) {
                        var list = res.data.data || [];
                        var nowSku = list[0] || {};
                        this.setData({
                            skuList: list,
                            nowSku
                        });
                    }

                    this.setData({
                        showBuyPopup: true,
                        goodsItem
                    });
                });
        },

        replaceSku(e) {
            //切换规格
            var item = e.currentTarget.dataset.item;
            this.setData({
                nowSku: item
            });
        },

        /** 加入购物车 */
        addCart() {
            var nowSku = this.nowSku;
            var marchantId = this.marchantId;
            var buyCount = this.buyCount;

            if (nowSku.stock < buyCount) {
                uni.showToast({
                    title: '库存不足',
                    icon: 'none',
                    duration: 2000
                });
                return;
            }

            var data = {
                tempSpecId: nowSku.id,
                commodityId: nowSku.commodityId,
                amount: this.buyCount,
                marchantId: marchantId,
                operate: 1
            };
            app.globalData.sjrequest('/commodity/addTrolley', data).then((res) => {
                if (res.data.code == 200) {
                    uni.showToast({
                        title: '添加成功',
                        icon: 'success'
                    });
                    this.closeBuyPopup();
                }
            });
        },

        /**立即购买 */
        buyNow() {
            var nowSku = this.nowSku;
            var orderTemplate = this.orderTemplate;
            var marchantId = this.marchantId;
            var buyCount = this.buyCount;

            if (buyCount > nowSku.stock) {
                uni.showToast({
                    title: '库存不足',
                    icon: 'none',
                    duration: 2000
                });
                return;
            }

            let data = {
                marchantId,
                orderType: 3,
                commoditys: [
                    {
                        commodityId: nowSku.commodityId,
                        tempSpecId: nowSku.id,
                        amount: buyCount
                    }
                ]
            };
            uni.showLoading({
                title: '加载中...'
            });
            var token = uni.getStorageSync('token');
            app.globalData.sjrequest1('/order/onekeyAboutOrder', data, token).then((res) => {
                if (res.data.code === 200) {
                    uni.hideLoading();
                    app.globalData.store.setState({
                        // 更新 store 数据
                        submitObj: JSON.stringify(res.data.data)
                    });
                    var url = `/pages/order/submitOrder/submitOrder?`;
                    url += `orderType=${orderTemplate}`;
                    uni.navigateTo({
                        url
                    });
                } else {
                    uni.hideLoading();
                    uni.showToast({
                        title: res.data.msg,
                        icon: 'none'
                    });
                }
            });
        }
    }
};
</script>
